@charset "utf-8";
@import "_format";
@import "_extend";
section {
  width: 100%;
  height: 100vh;
  position: relative;
  h1,
  h2 {
    @include fz(62);
    font-family: $font_teko;
    line-height: 1em;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .fig {
    max-width: 400px;
    width: 40%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    img {
      width: 100%;
    }
  }
  &.sec__01 {
    background: #c7b9a6;
    color: #4b4044;
  }
  &.sec__02 {
    background: #948374;
    color: #4b4044;
    .fig {
      left: 60%;
    }
  }
  &.sec__03 {
    background: #665b5f;
    color: #000;
    .fig {
      left: 40%;
    }
  }
  &.sec__04 {
    background: #4b4044;
    color: #c7b9a6;
    .fig {
      left: 60%;
    }
  }
}
